.menu{
    position: relative;
    top: 0%;
    z-index: 34;
  }



.menu a{
    position: relative;
    text-align: center;
    float: left;
    font-family: 'Montserrat',sans-serif;
    width: 25%;
    display: inline;
    margin: auto;
    /* font-family: ; */
    color:#f2f2f2;
    text-align: center;
    text-decoration: none;
    background-color:#003459;
    font-size: 30px;
}

.menu a:hover{
  background-color: black;
}

@media screen and (max-width: 830px) {
  .menu a{
      font-size: 20px;
  }

