html2, body2{
    margin: 0;
    background-color: #7F95D1;
}

.smaller{
    font-size: 3cm;
}

.text{
    color: white;

    background-color: midnightblue;
    text-align: center;
    font-size: 0.3in;
}

.percent{
    color: white;

    text-align: center;
    font-size: 0.7in;

}
@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');
.block{
    background-color: midnightblue;
    margin-left: 1cm;
    margin-top: 1cm;
    margin-right: 1cm;
    font-family: 'Raleway', cursive;
    padding: 1em;
    animation-name: slidein;
    animation-duration: 7s;
}
.last{
    margin-bottom: 1cm;
    animation-name: slidein;
    animation-duration: 7s;
}
