
body{
    margin: 0;
    position: static;
    background-color: #FFFFFF;
    text-align: center;
    justify-content: center;
    z-index: -1;
    overflow-x: hidden;
    /* animation-name: fadein;
    animation-duration:4s; */
}

@keyframes fadein{
    from{opacity: 0%;}
    to{opacity: 100%;}
}

.nav{
    position: relative;
    overflow: hidden;
    top: 0in;
    /* width: 100vh; */
    
    z-index: 5;
    
}
.col{
    align-items: center;
    display: flex;
    flex-direction: row;
}

.textbox-1{
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    position: relative;
    margin-top: 1cm;
    margin: 0.5cm;
    width: 30%;
    background-color: #1d76c4;
    background-image: url(cutcosts.jpg);
    background-blend-mode:darken;
    color: white;
    font-style: normal;
    font-family: 'Montserrat',sans-serif;
    padding: 1cm;
}
.textbox-2{
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    position: relative;
    margin-top: 1cm;
    margin: 0.5cm;
    width: 30%;
    background-color: #1d76c4;
    background-image: url(homepage.jpeg);
    background-blend-mode:overlay;
    color: white;
    font-style: normal;
    font-family: 'Montserrat',sans-serif;
    padding: 1cm;
}

.textbox-3{
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    position: relative;
    margin-top: 1cm;
    margin: 0.5cm;
    width: 30%;
    background-color: #1d76c4;
    background-image: url(oh.jpg);
    background-blend-mode:overlay;
    color: white;
    font-style: normal;
    font-family: 'Montserrat',sans-serif;
    padding: 1cm;
}
.col h1{
    text-align: left;
    float: left;
    font-size: 0.8in;
}
.col span{
    text-align: left;
    float: left;
    font-size: 0.5in;
    padding-bottom: 0.5in;

}
.bar{
    background-color: black;
    width: 1cm;
    right: 0;
}

.header-bar{
    display: flex;
    flex-direction: column;
    position: relative;
    width: 98.545%;
    background-image:url(homepage.jpeg);
    background-blend-mode: overlay;
    background-color: #2e95b8;
    padding: 1cm;
    
}
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
.header-bar h1{
    color: #FFFFFF;
    font-size: 2in;
    align-items:left;
    text-align: left;
    float: left;
    font-style: normal;
    font-family: 'Montserrat',sans-serif;
    padding-left: 2cm;
    padding-bottom: 0in;
    margin: 0cm;
}
.header-bar h2{
    color: #FFFFFF;
    font-size: 0.4in;
    align-items:left;
    text-align: left;
    float: left;
    margin-top: 0;
    font-style: normal;
    margin-left: 1cm;
    font-family: 'Montserrat',sans-serif;
}

.footer{
    margin-right: auto;
    margin-left: auto;
    bottom: 0;
    width: 100%;
    position: fixed;
    color: black;
    font-size: 1cm;
    font-family: 'Montserrat',sans-serif;
    background-color: #cecece;
  }

.textbox-1:hover{
    padding: 2cm;
}
.textbox-2:hover{
    padding: 2cm;
}
.textbox-3:hover{
    padding: 2cm;
}


@media screen and (max-width: 900px){
    .col{
        align-items: center;
        display: flex;
        flex-direction: column;
    }
    .textbox-1{

        align-items: center;
        text-align: center;
        margin: 1cm;
        width: 100%;
        
        padding: 0.5cm;
    }
    .textbox-2{
        margin: 1cm;
        width: 100%;
        padding: 0.2cm;
    }
    
    .textbox-3{
        margin: 1cm;
        width: 100%;
        padding: 0.2cm;
    }
    .col h1{
        float: center;
        margin-bottom: 0;
        margin-left: 0.5cm;
        font-size: 0.3in;
    }
    .col span{
        font-size: 0.1in;
        margin-left: 0.5cm;
        padding-bottom: 0.2in;
    
    }
    
    
    .header-bar{
        width: 100%;
        padding: 0.2cm;
        
    }
   
    .header-bar h1{
        font-size: 0.5in;
        padding-left: 1cm;
        margin: 0cm;
    }
    .header-bar h2{
        font-size: 0.1in;
        padding-left: 0.5cm;
    }
    
}

@media screen and (max-width: 2000px) {
    .col{
        overflow:visible;
        align-items: center;
        display: flex;
        text-align: center;
        flex-direction: row;
    }
    .textbox-1{
        
        align-items: center;
        
        
        margin-top: 0.7cm;
        width: 100%;
        
        padding: 0.5cm;
    }
    .textbox-2{
        align-items: center;
        text-align: center;
        margin-top: 0.7cm;
        width: 100%;
        padding: 0.2cm;
    }
    
    .textbox-3{
        align-items: center;
        text-align: center;
        margin: 0.7cm;
        width: 100%;
        margin-bottom: 0.7cm;
        padding: 0.2cm;
    }
    .col h1{
        
        float: center;
        margin-left: 0.5cm;
        font-size: 0.4in;
    }
    .col span{
        font-size: 0.3in;
        margin-left: 0.5cm;
        padding-bottom: 0.2in;
    
    }
    
    .footer{
        position: relative;
    }
    .header-bar{
        overflow: hidden;
        width: 100%;
        padding: 0.2cm;
        
    }
   
    .header-bar h1{
        font-size: 1in;
        padding-left: 1cm;
        margin: 0cm;
    }
    .header-bar h2{
        font-size: 0.3in;
        padding-left: 0.5cm;
    }
    
}
